

.journal__main-content {
    display: flex;
}

.journal__sidebar {
    background-color: $dark;
    color: white;
    display: flex;
    flex-direction: column;
    height: 100vh;
    padding-left: 10px;
    padding-right: 10px;
    width: 450px;
}

.journal__sidebar-navbar {
    display: flex;
    justify-content: space-between;
}

.journal__sidebar-navbar span {
    font-size: 13px;
    margin-left: 10px;
}


.journal__sidebar-navbar h3 {
    font-weight: lighter;
}

.journal__sidebar-search input {
    display: flex;
    color: grey;
    flex-direction: column;
    width: 50%;
    height: 25px;
    margin-left: 75px;
    border: 0;
}

.journal__sidebar-google-img {
    width: 30px;
    height: 30px;
    border-radius: 100px;
}


.journal__new-entry {
    align-items: center;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 30px;
    width: 100%;

    transition: color .3s ease;

    &:hover {
        color: darken($color: white, $amount: 20);
    }
    
}

.journal__entries {
    flex: 1 1 auto;
    margin-top: 30px;
    overflow-y: auto;

    &::-webkit-scrollbar {
        -webkit-appearance: none;
        //background-color: #ccc;
    }
 
    &::-webkit-scrollbar:vertical {
        width: 8px;
    }
 
    &::-webkit-scrollbar:horizontal {
        height: 10px;
    }
 
    &::-webkit-scrollbar-thumb {
        background-color: #797979;
        border-radius: 20px;
    }
}

.journal__entry {
    background-color: white;
    border-radius: 4px;
    color: $dark;
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    overflow: hidden;
}

.journal__entry-picture {
    height: 75px;
    width: 75px;
}


.journal__entry-body {
    padding: 10px;
}

.journal__entry-title {
    font-size: 14px;
    font-weight: bold;
    padding: 5px;
}

.journal__entry-content {
    font-size: 10px;
    padding: 5px;
}

.journal__entry-date-box {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 5px;
}

.journal__entry-date-box span{
    font-size: 12px;
}
