

.auth__main {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $light-grey;
    background-image: url("https://res.cloudinary.com/dk3yfcnpo/image/upload/v1612392354/zbuy7wdvkprgoro65szg.png");
    margin: 0;
    height: 100vh;
    width: 100vw;
}

.auth__box-container {
    background-color: white;
    border: 1px solid $primary;
    box-shadow: 1px 1px $light-grey;
    border-radius: 2px;
    padding: 20px;
    width: 250px;
}

.auth__title {
    color: darken($color: $primary, $amount: 20);
    margin-bottom: 20px;
    text-align: center;
}

.auth__input {
    color: $dark;
    border: 0px;
    border-bottom: 1px solid $light-grey;
    font-size: 16px;
    margin-bottom: 10px;
    height: 20px;
    width: 100%;

    transition: border-bottom .3s ease;

    &:focus{
        border-bottom: 1px solid $primary;
        outline: none;
    }
}

.auth__social-networks {
    align-items: center;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding-top: 20px;
    padding-bottom: 20px;
    width: 100%;
}

.auth__alert-error {
    background-color: #EEB3B3;
    border: 1px solid #EA3F25;
    color: lighten($color: black, $amount: 30);
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
    padding: 5px;
}

.auth__loading-page {
    position: fixed;
    background-color: white;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    z-index: 9999;
    background: url('https://i.gifer.com/YCZH.gif') 50% 50% no-repeat rgb(249,249,249);
    opacity: .8;
}